import * as Vue from 'vue';
import {wgs2lv95} from '../coords/coords.js';
import BoundsForm from '../bounds/BoundsForm.vue';
import CoordsForm from '../coords/CoordsForm.vue';
import ProjectTools from './ProjectTools.vue';

// TODO(https://github.com/buildrs/xyz/issues/1): CSS loading is weird.
import css from '../../public/index.css';


let comp;
let map, marker, boundsRect;

const bern = { lat: 46.951082773, lng: 7.438632421 };
[bern.N, bern.E] = wgs2lv95(bern.lat, bern.lng);

const app = Vue.createApp({
  components: {
    'coords-form': CoordsForm,
    'bounds-form': BoundsForm,
    'project-tools': ProjectTools
  },
  data() {
    return {
      coordinate: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,
      },
      min: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,
        /*
        lat: 0,
        lon: 0,
        N: 0,
        E: 0,*/
      },
      max: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,/*
        lat: 0,
        lon: 0,
        N: 0,
        E: 0,*/
      },
      pri_coordinate: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,
      },
      pri_min: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,/*
        lat: 0,
        lon: 0,
        N: 0,
        E: 0,*/
      },
      pri_max: {
        lat: bern.lat,
        lon: bern.lng,
        N: bern.N,
        E: bern.E,/*
        lat: 0,
        lon: 0,
        N: 0,
        E: 0,*/
      },
      pri_altitude: 0,
      pri_northing: 0
    }
  },
  created() {
    //console.log(`created:`, this.coordinate);
    comp = this;
    window.comp = comp;
  },
  methods: {
    onCoordChanged(ev) {
      this.pri_coordinate.lat = ev.wgs.lat;
      this.pri_coordinate.lon = ev.wgs.lon;
      this.pri_coordinate.N = ev.lv95.N;
      this.pri_coordinate.E = ev.lv95.E;
      //console.log('main.js#onCoordChanged', this.pri_coordinate);
      centerMap(ev.wgs.lat, ev.wgs.lon);
    },
    onBounds(ev) {
      console.log(ev);
      this.pri_min.lat = ev.min.wgs.lat;
      this.pri_min.lon = ev.min.wgs.lon;
      this.pri_min.N = ev.min.lv95.N;
      this.pri_min.E = ev.min.lv95.E;
      this.pri_max.lat = ev.max.wgs.lat;
      this.pri_max.lon = ev.max.wgs.lon;
      this.pri_max.N = ev.max.lv95.N;
      this.pri_max.E = ev.max.lv95.E;
      const boxCoords = [
        { lat: this.pri_min.lat, lng: this.pri_min.lon },
        { lat: this.pri_min.lat, lng: this.pri_max.lon },
        { lat: this.pri_max.lat, lng: this.pri_max.lon },
        { lat: this.pri_max.lat, lng: this.pri_min.lon },
        { lat: this.pri_min.lat, lng: this.pri_min.lon },
      ];
      if (boundsRect) {
        boundsRect.setMap(null);
      }
      boundsRect = new google.maps.Rectangle({
        paths: boxCoords,
        strokeColor: "#000000",
        strokeOpacity: 1.0,
        strokeWeight: 3,
        map,
        bounds: {
          north: this.pri_max.lat,
          south: this.pri_min.lat,
          east: this.pri_max.lon,
          west: this.pri_min.lon,
        }
      });
      boundsRect.setMap(map);
      console.log(map, boundsRect);
      console.log('main.js#onBounds message received', this.pri_min, this.pri_max);
    }
  }
});
app.mount('#app');


// Initialize and add the map
window.initMap = () => {
  map = new google.maps.Map(document.getElementById("map"), {
    zoom: 17,
    center: bern,
  });
  marker = new google.maps.Marker({
    position: bern,
    map: map,
  });
};


function centerMap(lat, lon) {
  // Avoid race at startup
  if (marker && map) {
    marker.setPosition(new google.maps.LatLng(lat, lon));
    map.panTo(new google.maps.LatLng(lat, lon));
  }
}
